import {Directive, HostBinding, Input, OnInit} from '@angular/core';
import {DomSanitizer, SafeStyle} from '@angular/platform-browser';

@Directive({
  selector: '[appCardShadow]'
})
export class CardShadowDirective implements OnInit {
  @HostBinding('style.backgroundColor') backgroundColor;
  @HostBinding('style.backgroundSize') backgroundSize;
  @HostBinding('style.background') background: SafeStyle;
  @Input() picture = '\'data:image/jpeg;base64,\'';

  constructor(private sanitizer: DomSanitizer) {
  }

  ngOnInit(): void {
    this.background = this.sanitizer.bypassSecurityTrustStyle(
      'url(' + this.picture + ') center center / cover no-repeat'
    );
  }

}
